import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom'
import { toast, ToastContainer } from 'react-toastify';
import { Navigation } from 'react-router-dom';
import 'react-toastify/dist/ReactToastify.css';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilLockLocked, cilUser } from '@coreui/icons'

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loginData, setLoginData] = useState(null);
  const history = useNavigate();
  var responseData = {};

  const handleLogin = () => {
    const data = {
      "domain": "application",
      "UserLogin": email,
      "password": "123"
    };
    setLoginData(responseData);

    fetch('https://apicargodev.azurewebsites.net/api/Login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
      .then(response => response.json())
      .then(data => {


        // Trate a resposta do servidor conforme necessário
        if (data.authenticated) {
          toast.success('Login realizado com sucesso!',);
          window.location.href = "/home";
        }
        else{
          toast.error('Erro ao fazer login:');
        }



      })
      .catch(error => {

        toast.error('Erro ao fazer login:');
      });
  };


  const handleEmailChange = (event) => {
    setEmail(event.target.value);
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };
  return (
    <div className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md={8}>
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm>
                    <h1>Login</h1>
                    <p className="text-medium-emphasis"></p>
                    <CInputGroup className="mb-3">
                      <CInputGroupText>
                        <CIcon icon={cilUser} />
                      </CInputGroupText>
                      <CFormInput
                        placeholder="Email"
                        autoComplete="username"
                        value={email}
                        onChange={handleEmailChange}

                      />
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupText>
                        <CIcon icon={cilLockLocked} />
                      </CInputGroupText>
                      <CFormInput
                        type="password"
                        placeholder="Senha"
                        autoComplete="current-password"
                        value={password}
                        onChange={handlePasswordChange}
                      />
                    </CInputGroup>
                    <CRow>
                      <CCol xs={6}>
                        <CButton color="primary" className="px-4" onClick={handleLogin}>
                          Login
                        </CButton>
                      </CCol>
                      <CCol xs={6} className="text-right">
                        <CButton color="link" className="px-0">
                          {/* Coloque o conteúdo do botão de link aqui, se necessário */}
                        </CButton>
                        {/* Adicione o ToastContainer com o estilo para centralizar */}
      <ToastContainer
        autoClose={300}
        style={{
          top: '10%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
        }}
      />
                      </CCol>
                    </CRow>
                  </CForm>
                </CCardBody>
              </CCard>
              <CCard className="text-white bg-primary py-5" style={{ width: '44%' }}>
                <CCardBody className="text-center">
                  <div>
                    <h2>PepsiCo Login</h2>
                    <p>
                      Entre com sua conta SSO
                    </p>
                    <Link to={"https://secure.pepsico.com/oauth2/default/v1/authorize?client_id=0oa1sb2bvrhNCWSmS0h8&response_type=code&response_mode=query&scope=openid&redirect_uri=https://pepsicobrasil.sigla.app&state=1"}>


                      <CButton color="primary" className="mt-3" active tabIndex={-1}>
                        Login com SSO
                      </CButton>
                    </Link>
                  </div>
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  );
};


export default Login
